/* --- SUB NUVIGATION --- */

.subnavigation-bar {

}

.subnavigation-bar ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0.5rem 2rem;
}

.subnavigation-bar li {
    list-style-type: none;
    margin: 0 0.5rem;
}

.subnavigation-bar li a {
    font-size: larger;
    color: hsl(208, 100%, 50%);
}

.subnavigation-bar li a:hover {
    color: hsl(0, 95%, 44%);
}



/* --- PROJECTS GRID ---  */

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    gap: 2rem 1rem;

    margin: 1rem;
}

.projects-card img {
    display: block;
    width: 100%;
    border-radius: 10px;
}

.projects-card h3 {
    margin: 0.5rem 0;
}

.projects-card p {
    display: inline;
    margin: 0.1rem 0;
    font-style: italic;
    color: hsl(0, 0%, 50%);
}